.form-opt{
	margin: 80px auto;
	max-width: 1160px;
	background: #FFFFFF;
	box-shadow: 0px 0px 30px rgba(162, 165, 166, 0.35);
	padding: 20px;
	&__container{
		border: 1px solid rgba(201, 201, 209, 0.50);
		border-bottom: 2px solid #F74B4B;
		padding: 40px;
	}
	&__zag{
		font-size: 33px;
		line-height: 133%;
		text-align: center;
		color: #414141;
		margin-bottom: 20px;
	}
	&__p{
		text-align: center;
		font-size: 16px;
		line-height: 160%;
		color: #B0B0B0;
	}
	&__form{
		margin: 20px auto;
		padding-top: 40px;
		max-width: 920px;
		border-top: 1px solid #D8DFE2;
		form{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}
		.required--sbmt{
			padding: 12px 30px;
			font-weight: 500;
			font-size: 14px;
			line-height: 135%;
			color: #EF4949;
			border: 2px solid #EF4949;
			border-radius: 5px;
			text-transform: uppercase;
			background-color: #fff;
			transition: .3s;
			cursor: pointer;
			margin: 0 auto;
			&:hover{
				background-color: #EF4949;
				color: #fff;

			}
		}
		.input-text-label{
			width: 100%;
			margin-bottom: 40px;
			label{
				font-size: 16px;
				line-height: 120%;
				color: #000000;
				top: 20px;
			}
			input{
				width: 100%;
				padding: 20px;
				border: 0;
				border-bottom: 1px solid #ACACAC;
			}
			input.error{
				border-bottom-color: #EF4949;
			}
		}
		.input-text-label.focus label{
			top: -5px;
			color: grey;
			font-size: 12px;
		}
		.input-text-label.w2{
			width: calc(50% - 10px);
		}
	}
}